<template>
  <div class="receptionWrap">
    <div class="title">
      <div></div>
      <div>标本接收</div>
    </div>
    <el-tabs class="tabs"
             v-model="activeName"
             type="card"
             @tab-click="handleClick">
      <el-tab-pane label="标本接收"
                   name="first"
                   class="tabs1">
        <div class="tit">
          <div class="text">扫码接收</div>
          <div class="input1">
            <el-input v-model="searchValue.value"
                      placeholder="科室"></el-input>
          </div>
          <div class="input2">
            <el-input v-model="searchValue.value1"
                      placeholder="请扫描或输入接收条码"></el-input>
          </div>
          <div class="printBtn">
            <el-button type="primary"
                       size="small">接收单打印</el-button>
          </div>

        </div>
        <TableList />
      </el-tab-pane>
      <el-tab-pane label="接收清单"
                   name="second"
                   class="tabs2">
        <el-form :inline="true"
                 :model="formValue"
                 size="small"
                 class="demo-form-inline">
          <el-form-item label="条码号">
            <el-input v-model="formValue.one"
                      placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="接收人">
            <el-input v-model="formValue.two"
                      placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="标本状态">
            <el-select v-model="formValue.three"
                       placeholder="请选择">
            </el-select>
          </el-form-item>
          <el-form-item label="开单科室">
            <el-select v-model="formValue.four"
                       placeholder="请选择">
         
            </el-select>
          </el-form-item>
          <el-form-item label="接收时间">
            <el-date-picker v-model="formValue.five"
                            type="datetimerange"
                            :picker-options="pickerOptions"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期"
                            align="right">
            </el-date-picker>
          </el-form-item>
        </el-form>
        <div class="btnWarp">
          <el-button type="primary"
                     size="small">查&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;询</el-button>
          <el-button type="primary"
                     size="small">接收单打印</el-button>
          <el-button type="primary"
                     size="small">导&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;出</el-button>
        </div>
        <TableList />

      </el-tab-pane>
      <el-tab-pane label="核酸接收"
                   name="third">核酸接收</el-tab-pane>

    </el-tabs>
  </div>
</template>

<script>
import TableList from '@/components/specimenReception/TableList.vue'

export default {
  name: 'SpecimenReception',
  components: {
    TableList,
  },
  data() {
    return {
      activeName: 'first',
      searchValue: {
        value: '',
        value1: '',
      },
      formValue: {
        one: '',
        two: '',
        three: '',
        four: '',
        five: null,

      },
      pickerOptions: {
        shortcuts: [
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', [start, end])
            },
          },
          {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit('pick', [start, end])
            },
          },
          {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
              picker.$emit('pick', [start, end])
            },
          },
        ],
      },
    }
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event)
    },
  },
}
</script>

